import React from "react";
import { motion } from "framer-motion";

const Hero = () => {
  return (
    <section className="bg-gradient-to-r from-black via-gray-900 to-black text-white min-h-[80vh] flex items-center justify-center">
      <motion.div
        className="text-center max-w-3xl px-6"
        initial={{ opacity: 0, y: 40 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ duration: 0.8 }}
      >
        <h1 className="text-5xl md:text-6xl font-extrabold mb-6 leading-tight">
          Find <span className="text-blue-500">Your Style</span> with Us
        </h1>
        <p className="text-lg md:text-xl text-gray-300 mb-8">
          Explore premium categories and featured collections curated just for you.
        </p>
        <button className="px-8 py-3 bg-blue-600 rounded-xl shadow-md hover:bg-blue-700 transition text-lg font-medium">
          Shop Now
        </button>
      </motion.div>
    </section>
  );
};

export default Hero;
